
import axios from 'axios'
/*import Xheader from '@/components/comother/rheader'*/
import Vue from 'vue';
import ViewBox from 'vux/src/components/view-box/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

Vue.component(Popup.name, Popup);
export default {
data(){

return {
contents:{rights:'评分说明',title:'政治学习评分'},
list:"",
isYellow:false,
showPop:false
}
},
filters: {
Upper: function (value) {
try {
if(value=== 1)  throw "已拒绝";
if(value=== 2)  throw "已通过";
}
catch(err) {
return value=err;
}

},
Upper1: function (value) {
try {
/* if(value===null) throw "";*/
if(value=== 1)  throw "yellowB";
if(value=== 0)  throw "yellowC";
}
catch(err) {
return value=err;
}

}
},
components:{
XHeader,
ViewBox,
Popup,
Flexbox,
FlexboxItem,
},
directives:{
TransferDom
},
methods:{
getlist(){
axios({
method: 'get',
url: '/dangjian/pstudy/getPartymemberByDepartmentid',
params: {
departmentid:1,
activeType:this.$route.params.moduleid
}
}) .then((res)=> {
this.list=res.data;


})
.catch(function (error) {
console.log(error);
});
},
changeItem(item){
console.log(item.tempint)
if(item.tempint!=1){
this.$router.push({
path: '/points/pointEvaluate',
name: 'pointEvaluate',
params: {
partmentId:item.id,
name:encodeURI(item.name),
departmentId:item.departmentid,
userId:item.userid
}
})}
},
know(){
this.showPop = false
},
datePick(s){
Date.prototype.toLocaleString = function(){
return this.getFullYear() +'.'+ (this.getMonth()+1)+'.'+this.getDay()
}
return new Date(s).toLocaleString();
},
showMenu(){
this.showPop = true;
}
},
mounted(){
this.getlist();

}
}



